.showTask{
    padding: 20px 15px;
    border-radius: 5px;
    background-color: #FFFFFF;
    margin: 20px auto;
    box-shadow: var(--box-shadow);
    
  }


.showTask div.head{
    padding: 10px 0px 20px 0px;
    border-bottom: 1px solid #dfdfdf;
    display: flex;
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
  

.showTask div.head span:first-child{
    flex-grow: 1;
}

.showTask .title{
    font-size: 18px;
    font-weight: 600;
    user-select: none;
  }

  .showTask .count{
    background-color: #dfdfdf;
    color: #525252;
    padding: 5px 10px;
    border-radius: 50%;
    margin-left: 10px;
    user-select: none;
  }

  .showTask .clearAll{
    background-color: #0f6af3;
    color: #FFFFFF;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }

  .showTask .clearAll:hover{
    background-color: #1061db;
  }
  

  .showTask ul{
    margin: 30px 5px 20px 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 15px;
  }
  
  .showTask li{
    padding: 15px 10px;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    gap: 5px;
    width: 350px;
    box-shadow: var(--box-shadow);
    border-radius: 5px;
    border-left: 5px solid #1365dfaf;
  }
  
  .showTask li:hover{
    background-color: #f8f8f8;
  }
  
  .showTask li p{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  
  .showTask li .name{
    font-size: 18px;
    padding-bottom: 5px;
  }
  
  .showTask li .time{
    font-size: 12px;
    color: #737373;
  }
  
  .showTask i.bi-pencil-square{
    font-size: 18px;
    color: #1363DF;
    cursor: pointer;
  }
  
  .showTask i.bi-trash{
    font-size: 18px;
    color: #D82148;
    cursor: pointer;
  }
  
  @media only screen and (max-width: 800px) {
    .showTask li{
      width: 100%;
    }
  }